import React from "react"
import Header from "../components/Header"
import styled from "styled-components"
import "../components/layout.css"

const ProjectForm = styled.form`
  display: flex;
  flex-direction: column;
`

const ProjectTitle = styled.h1`
  font-size: 70px;
  margin-bottom: 10px;
  text-align: center;
`

const ProjectLabel = styled.label`
  text-align: center;
  margin: 1rem;
`

const ProjectInput = styled.input`
  width: 300px;
  height: 30px;
  padding: 0.5rem;
  outline: none;
  letter-spacing: 1px;
  background-color: #dbd5d5;
  border: none;
  margin: 5px auto 15px auto;
  border-radius: 2px;
`

const ProjectTextarea = styled.textarea`
  width: 300px;
  height: 120px;
  padding: 0.5rem;
  resize: none;
  margin: 0 auto;
  border: none;
  outline: none;
  letter-spacing: 1px;
  background-color: #dbd5d5;
  border-radius: 2px;
`

const ProjectButton = styled.button`
  width: 175px;
  cursor: pointer;
  color: #fff;
  border: none;
  outline: none;
  font-weight: 500;
  height: 50px;
  background-color: #252d4f;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  -webkit-box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  box-shadow: -2px 0px 20px 4px rgb(0 0 0 / 25%);
  margin: 20px auto 0 auto;
`

const contact = () => {
  return (
    <div className="wrapper">
      <Header />
      <ProjectTitle>Contact</ProjectTitle>
      <ProjectForm name="contact" data-netlify="true" method="POST">
        <ProjectLabel for="name">Your Name</ProjectLabel>
        <ProjectInput type="text" id="name" required />
        <ProjectLabel for="email">Your Email</ProjectLabel>
        <ProjectInput type="email" id="email" required />
        <ProjectLabel for="message">Your Message</ProjectLabel>
        <ProjectTextarea type="text" id="message" required />
        <ProjectButton type="submit">Send Message</ProjectButton>
      </ProjectForm>
    </div>
  )
}

export default contact
